.location-card {
  display: block;
  text-align: center;
  border: 1px solid #e6e6e6;
  border-radius: 2px;
  padding-bottom: 10px; }
  .location-card:hover, .location-card:focus {
    box-shadow: 0 0 0 1px #e6e6e6, 2px 2px 0 0 #cacaca; }
    @media screen and (min-width: 64em) {
      .location-card:hover, .location-card:focus {
        box-shadow: 0 0 0 1px #e6e6e6, 3px 3px 0 0 #cacaca; } }
  .location-card__pic {
    display: block;
    width: 100%;
    margin-bottom: 16px; }
  .location-card__title {
    text-align: center; }
  @media screen and (min-width: 48em) {
    .location-card {
      padding-bottom: 16px; }
      .location-card__pic {
        display: block;
        width: 100%;
        margin-bottom: 22px; } }
  @media screen and (min-width: 64em) {
    .location-card {
      width: 100%; } }
